.dogs {
  margin: 30px 0;
  min-height: 100%;
}

.dogContainer {
  max-width: 90%;
  margin: 30px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
}

.loadContainer{
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading{
  width: 100%;
}

.notFoundContainer{
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #38002f;
}
.notFound {
  width: 100%;
}